import React, { Component } from 'react';
import axios from 'axios';

import './FindInfo.css';

class FindInfo extends Component {
  constructor(props) {
    super(props);
    this.state={
      email: null
    }
  }

  handleChange = (e) => {  
    this.setState({ email: e.target.value })
  }

  handleTempPwd = () => {
    this.state.email !== null
      && axios.post( 
          'FindInfo', 
          'email='+this.state.email, 
          { 'Content-Type': 'application/x-www-form-urlencoded' }
        )
        .then( response => {
          if(response.data === 'emailError') alert('  .')
          else if(response.data === 'complete') {
            alert(' 1~2      \n(         )')
          } 
          else alert(response.data)
        }) 
        .catch( response => { console.log('err\n'+response); } ) // ERROR
  }

  handleBackHome = () => {
    window.location = "/";
  }
  
  render() {
    console.log(this.state.email)
    return (
      <div>
        <a href="/"><img class="findInfo_logo" src={require('../img/common/logo_01.png')} /></a>
        <div class="findInfo">
          <h1 class="findInfo_title"> </h1>
          <input id="inputFindInfo" type="text" placeholder=" ." name="email" onChange={this.handleChange}/>
          <button id="tempPwd" onClick={this.handleTempPwd}><img src={require('../img/common/btn_17.png')} /></button>
        </div>
        <h6 class="tempPwdText">   .</h6>
        <button id="backToHome" onClick={this.handleBackHome}><img src={require('../img/common/btn_18.png')} /></button>
      </div>      
    );
  }
}

export default FindInfo;




